import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";
import router from "../router";
import uz from '../lang/uz.json'
import ru from '../lang/ru.json'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    direction:[],
    directionId:[],
    requs:[],
    abouts:[],
    structure:[],
    projects:[],
    projectsid:[],
    newss:[],
    newssid:[],
    appeals:[],
    docs:[],
    reglaments:[],
    managements:[],
    language:[],
    refCount:0,
    isLoading:false,
  },
  mutations: {
    loading(state, isLoading) {
      console.log({isLoading})
      if (isLoading) {
        state.refCount++;
        state.isLoading = true;
      } else if (state.refCount > 0) {
        state.refCount--;
        state.isLoading = (state.refCount > 0);
      }
    },
  },
  actions: {
    change({state}){
      if(localStorage.getItem('lang')=='ru'){
        state.language=ru
        console.log(state.language)
      }
      if(localStorage.getItem('lang')=='uz-latn'){
        state.language=uz
        console.log(state.language)
      }
      else {
        state.language=ru
      }
    },
    direction({state}){
      axios.get('/api/direction/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.direction=res.data
          })
    },
    directionid({state}){
      axios.get('/api/direction/'+router.currentRoute.params.id,{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.directionId=res.data
          })
    },
    requ({state}){
      axios.get('/api/requisite/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.requs=res.data
          })
    },
    about({state}){
      axios.get('/api/about/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.abouts=res.data
          })
    },
    structure({state}){
      axios.get('/api/structure/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.structure=res.data
          })
    },
    project({state}){
      axios.get('/api/project/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.projects=res.data
          })
    },
    projectId({state}){
      axios.get('/api/project/'+router.currentRoute.params.id+'/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.projectsid=res.data
          })
    },

    news({state}){
      axios.get('/api/news/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.newss=res.data
          })
    },

    newsid({state}){
      axios.get('/api/news/'+router.currentRoute.params.id+'/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.newssid=res.data
          })
    },

    appeal({state}){
      axios.get('/api/appeal/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.appeals=res.data
          })
    },
    docs({state}){
      axios.get('/api/docs/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.docs=res.data
          })
    },
    reglament({state}){
      axios.get('/api/reglament/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.reglaments=res.data
          })
    },
    management({state}){
      axios.get('/api/management/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.managements=res.data
          })
    },
  },



  modules: {
  }
})
