<template>
  <div id="app">
    <div class="loader" v-if="isLoading">
      <div class="ball_father">
        <div>
          <div class="d-block logo_fff">
            <div class="class1">
              <div class="class2">
                <div class="class3">
                  <div class="class4"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <router-view/>
  </div>
</template>
<script>
import {mapState} from 'vuex';

export default {
  name: "App",
  components: {

  },
  mounted() {
    document.title=this.$store.state.language.lang[11]
  },
  computed: {
    ...mapState(['isLoading', 'refCount'])
  },
  created() {
    this.$http.interceptors.request.use((config) => {
      this.$store.commit('loading', true);
      return config;
    }, (error) => {
      this.$store.commit('loading', false);
      return Promise.reject(error);
    });

    this.$http.interceptors.response.use((response) => {
      this.$store.commit('loading', false);
      return response;
    }, (error) => {
      this.$store.commit('loading', false);
      return Promise.reject(error);
    });
  }
};

</script>
